import React, { PureComponent } from 'react';
import { NavBar, Icon, } from 'antd-mobile';
import { connect } from 'react-redux';
import Content from '../../views/common/Content';

import '../../assets/css/traffic.css';

class TrafficAirScreen extends PureComponent
{
    render()
    {
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            icon={<Icon type="left" />}
                            onLeftClick={() => this.props.history.goBack()}>
                            航空
                        </NavBar>
                }
                <Content style={{backgroundColor: '#efeff4'}}>
                    <div className="trafficDetailCard">
                        <img src={require('../../assets/images/traffic/plane_1.jpg')} alt='' className="trafficDetailImage"/>
                        
                        <div className="trafficDetailNameRow">
                            <div className="trafficDetailNames">
                                <div className="trafficDetailName">
                                    郑州新郑国际机场
                                </div>
                                <div className="trafficDetailSubName">
                                    Zhengzhou Xinzheng International Airport
                                </div>
                            </div>
                            <div className="trafficDetailNameFlag">
                                4F级机场
                            </div>
                        </div>

                        <div className="trafficDetailLR">
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">地址</div>
                                <div className="trafficDetailLRSubItem">河南省郑州市郑州航空经济综合实验区</div>
                            </div>
                            <div className="trafficDetailSplitLR"/>
                            <div className="trafficDetailLRItem">
                                <div className="trafficDetailLRSubItem">旅客吞吐量</div>
                                <div className="trafficDetailLRSubItem">2430万人次（2017年））</div>
                            </div>
                        </div>

                        <div className="trafficDetailIntro">
                            <div className="trafficDetailIntroLine"/>
                            <div className="trafficDetailIntroText">机场简介</div>
                            <div className="trafficDetailIntroLine"/>
                        </div>

                        <div className="trafficDetailContent">
                            <div className="trafficDetailTitle1">郑州新郑国际机场</div>
                            <div className="trafficDetailLongText">
                                郑州新郑国际机场（Zhengzhou Xinzheng International Airport）（ICAO机场代码：ZHCC；IATA机场代码：CGO），简称“新郑机场”，别称“轩辕机场”，于1997年8月28日建成通航。是中部地区首个拥有双航站楼、双跑道的机场，华中地区级别最高的飞机检修基地，是实现高速公路、地铁、高铁等多种交通方式无缝衔接的综合交通换乘中心。郑州新郑国际机场是中国八大区域性枢纽机场之一、中国四大货运机场之一，货邮运增幅全球最快（中国第一，中西部首位）。郑州机场的货运航线已通达全球主要货运集散中心，初步构建起了以郑州为亚太物流中心、以卢森堡为欧美物流中心，覆盖全球的航空货运网络。以郑州新郑国际机场为核心的郑州航空经济综合实验区是中国唯一一个国家级航空港经济综合实验区。
                            </div>

                            <div className="trafficDetailTitle1">机场简介</div>
                            <div className="trafficDetailLongText">
                                郑州新郑国际机场是国内干线运输机场和国家一类航空口岸，目前已通航国内外67个城市地区，开通航线90条，其中国内84条，国际和地区6条。2008年，被国家民航局确定为全国八大区域性枢纽。新郑国际机场和多个国家有着航班往来。现有20多家国际航空公司开通郑州包机航线67条，开通有俄罗斯、新加坡、日本等亚太地区国家的不定期包机和澳门、台北的定期包机航线，还有飞往韩国首尔、香港的定期国际航线，每周出发航班817个，通达67个城市。
                            </div>

                            <div className="trafficDetailTitle1">航站楼、设施</div>
                            <div className="trafficDetailLongText">
                                郑州新郑国际机场T1航站楼所有航班转至T2航站楼运营，T1航站楼停用改造。
                            </div>
                            <div className="trafficDetailLongText">
                                郑州新郑国际机场T2航站楼地下2层，地上4层，东西向长407米，南北向长1128米，建筑面积48.45万平米。郑州新郑国际机场T2航站楼于2015年12月19日试运营，2015年12月22日正式投入运营。
                            </div>

                            <div className="trafficDetailTitle1">航空公司</div>
                            <div className="trafficDetailLongText">
                                截至2016年底，在郑州新郑国际机场运营客运航空公司40家（国内31家、国际9家），开通客运航线162条（其中国际地区25条），客运通航城市86个（其中国际地区19个），郑州新郑国际机场的航线网络通达性进一步增强，已成为国内除上海浦东、广州白云、深圳宝安机场之外的第四大货运机场；在全球前20位货运枢纽机场中，开通12个航点，基本形成覆盖欧美和东南亚主要货运枢纽的航线网络。
                            </div>

                            <div className="trafficDetailTitle1">航线情况介绍</div>
                            <div className="trafficDetailLongText">
                                一、国内客运通航城市（51航点）北京、包头、重庆、成都、长春、大庆、大连、恩施、福州、广州、桂林、贵阳、海口、哈尔滨、呼和浩特、海拉尔、杭州、济南、晋江、揭阳、昆明、库尔勒、兰州、丽江、绵阳、南宁、宁波、南通、青岛、上海、三亚、深圳、沈阳、神农架、天津、太原、台州、乌鲁木齐、温州、无锡、西双版纳、西安、厦门、襄阳、西宁、烟台、盐城、银川、珠海、张家界、遵义。
                            </div>
                            <div className="trafficDetailLongText">
                                二、国际及地区通航城市（49个）雅典、阿姆斯特丹、奥克兰、曼谷、巴厘岛、巴塞罗那、波士顿、柏林、布鲁塞尔、芝加哥、杜塞尔多夫、法兰克福、福冈、香港、胡志明市、夏威夷•火奴鲁鲁、汉堡、雅加达、高雄、吉隆坡、伦敦、洛杉矶、澳门、墨尔本、米兰、马德里、曼彻斯特、慕尼黑、马累、莫斯科、纽约、名古屋、大阪、巴黎、普吉岛、釜山、罗马、首尔、悉尼、新加坡、旧金山、西雅图、斯德哥尔摩、台北、东京、多伦多、台中、温哥华、苏黎世。
                            </div>
                        </div>
                    </div>
                </Content>
            </div>
        );
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
        isFromNativeApp: state.native.isFromNativeApp
    };
};

TrafficAirScreen = connect(mapStateToProps)(TrafficAirScreen);

export default TrafficAirScreen;


